@import "/src/Resources/scss/customs-mixin.scss";

#UserCard {
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.1);

    &:hover {
        transform: translateY(-5px);
    }
    .icons-wrapper {
        @include displayRow(7px, end, center);
        padding: 7px;
        padding-top: 0px;
        margin-right: 3px;

        .clock-color  {
            color: #ff9f43;
        }

        .alert-color {
            color: #DB3A34;
        }

        .alert-trinagle-color {
            color: #ff9f43;
        }
    }


    .badge-style {
        font-family: Rubik;
        font-style: normal;
        font-weight: normal;
        font-size: 12px;
        line-height: 14px;
        text-align: center;
        margin-right: 5px;

        &.admin {
            color: #528DFF !important;
            background-color: rgba(82, 141, 255, 0.12) !important;
        }
    }

    .icon-color {
        color: #1B9AAA;
    }

    .card-header-style {
        display: flex;
        justify-content: space-between;
    }

    .name-text {
        font-size: 18px;
        line-height: 21px;
        color: #2C2C26;
    }

    .user-name-text {
        font-weight: 300;
        font-size: 14px;
        line-height: 21px;
    }

    .content-group {
        @include displayRow(5px, space-between, center);
        margin-top: 10px;
    }

    .group-icon-text {
        @include displayRow(5px, baseline, center);
    }

    .click-card {
        cursor: pointer;
    }

    .label {
        font-size: 12px;
        line-height: 21px;
        text-align: center;
        color: #2C2C26;
    }

    .text {
        font-weight: 300;
        font-size: 12px;
        line-height: 21px;
        text-align: center;
        color: #2C2C26;
    }
}