@import "Resources/scss/variables/_variables.scss";

#SideBarModal {
    left: 0%;
    z-index: -1;
    padding: 0px;
    height: 100%; 
    min-width: 350px; 
    padding-left: 5px;
    border-radius: 5px;
    position: absolute;
    overflow: hidden;
    background-color: #F8F8F8;

    //<--Transition-->
    opacity: 0;
    transition: left 0.3s, opacity 0.5s linear;
    
    &.wrapper-sidebar {
        left : 99%;
        opacity: 1;
    }
    .nav-header {
        width: auto;
        background-color: #fff;
        box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.075);
    }

    .body-sidebar {
        height: 95%;
    }

    .wrapper {
        width: 100%;
        height: 100%;
        padding: 20px;
        display: flex;
        padding-top: 0;
        flex-direction: column;
    }

    .checkbox-text {
        max-width: 85%;
        font-style: italic;
        font-size: 13px;
        line-height: 14px;
        color: #5F5974;
    }

    .group {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 20px;
    }

    .title {
        font-weight: 500;
        font-size: 20px;
        line-height: 24px;
        color: $primary;
    }

    .dropezone-text {
        width: 218px;
    }
    .invalid {
        color: #ea5455;
    }
}