
#UserPage {
    height: 100%;

    .filters-container  {
        gap: 15px;
        width: 60%;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    }

    .wrapper-sort {
        gap: 5px;
        display: flex;
        align-items: center;
    }

    .group {
        gap: 5px;
        display: flex;
        align-items: center;
    }

    .wrapper-filters {
        display: flex;
        align-items: baseline;
        justify-content: space-between;
    }
    .cards-container {
        gap: 1.25rem;
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(22.5rem, 1fr));
    }

    .search-select {
        & .css-1s2u09g-control {
            background-color: transparent !important;
            border: none !important;
        }
        .css-1pahdxg-control {
            background-color: transparent !important;
            border: none !important;

        }
    }
}