#ModalUsers {
    &.sidebar-active {
        margin-right: 35%;
        
        .modal-header {
            .btn-close {
                transform: translate(360px, -10px);

                &:active {
                    transform: translate(360px, -2px)!important; 
                }
                &:hover {
                    transform: translate(360px, -2px)!important;
                }
                &:focus {
                    transform: translate(360px, -2px)!important;
                }
            }
        }
        .modal-body {
            padding-bottom: 100px;
        }
    }

    .img-form-wrapper {
        display: flex;
        gap: 20px;
    }
    
    .name-row {
        display: flex;
        gap : 5px;
        justify-content: space-between;
    }

    .user-row {
        display: flex;
        justify-content: space-between;
    }

    .contact-data-row {
        display: flex;
        gap : 20px;
        width: 100%;
        justify-content: space-between;
    }

    .education-section {
        display: grid;
        column-gap: 20px;
        grid-template-columns: repeat(2, 1fr);
    }

    .title-education {
        font-weight: 500;
        font-size: 22px;    
        line-height: 26px;
        color: #5F5974;
    }

    .text-error {
        font-size: 0.8rem;
        color: #f44336;
        font-weight: 500;
    }

    .invalid {
        margin-bottom: 0!important;
        border-color: #f44336 !important;

        svg {
            color: #f44336 !important;
        }
    }
}